import React from "react";
import {Context} from "../../Context";



export const Search = ({startSearch, toggleSearchFilter, visibleFilter}) => {
    let {handleChange, value} = React.useContext(Context);
    let ref = React.createRef()

    return(
        <div className="search" onClick={toggleSearchFilter}>
            <input
                type="text"
                onChange={handleChange}
                value={value}
                placeholder={"chose filter and then enter value..."}
                ref={ref}
            />
        </div>
    )
}
