import React from "react";
import {Friend} from "./friend/Friend";
import {Context} from "../../Context";
import {Preloader} from "../../preloader/Preloader"
import "./allFriends.scss"



export const AllFriends = () => {
    let {friends, clickId, value, filter, isFetching} = React.useContext(Context);

    const searchBy = (name) => name.includes(value);

    const filterBy = (status, gender) => {
        switch (filter) {
            case "alive":
                return status === "Alive";
            case "death":
                return status === "Dead";
            case "male":
                return gender === "Male";
            case "status unknown":
                return gender === "unknown";
            case "female":
                return gender === "Female";
            default:
                return true
        }
    }

    return (
        <section className={"allFriends"}>
            <div className="container">
                <h2>All friends</h2>
                <div className="row">
                    {
                        friends.map(({id, image, actor, yearOfBirth, created, name, location, gender, status}, i) => {
                            let year = yearOfBirth !== "" ? `${new Date().getFullYear() - parseInt(yearOfBirth)}` : "unknown";

                            return (
                                <React.Fragment key={id}>
                                    {
                                        searchBy(name) &&
                                        filterBy(status, gender) &&
                                        <Friend
                                            id={id}
                                            idForLink={i}
                                            img={image}
                                            name={name}
                                            age={new Date(created).toLocaleDateString()}
                                            house={location.name}
                                            gender={gender}
                                            key={id}
                                            clickId={clickId}
                                            isFetching = {isFetching}
                                            status = {status}
                                        />
                                    }
                                </React.Fragment>
                            )
                        }
                    )
                    }
                </div>
            </div>
            {isFetching && <Preloader/>}
        </section>
    )
}